import React, { Component } from 'react'
import { Modal, Avatar, Icon, Input, Descriptions, Statistic, Row, Col, message, Form, Select, notification } from 'antd'
import { Link } from 'react-router-dom'
import { getTotal, getUrl, addUrl } from '../../api'
import './index.less'

const { Search } = Input;
const { Option } = Select;
const { Item } = Form
class UrlForm extends Component<any> {
  state = {
    visible: false,
    postCount: 0,
    urlCount: 0,
    articalCount: 0,
    todoCount: 0,
    dailyCount: 0,
    footCount: 0,
    photoCount: 0,
    featCount: 0,
    money: 0,
    urls: [[], [], [], [], [], [], [], [], [], [], []],
    newUrls: [[], [], [], [], [], [], [], [], [], [], []],
    title: '',
    url: '',
  }

  componentDidMount() {
    this.getData()
  }

  getData() {
    getTotal({}).then((res: any) => {
      if (res.data && res.data.success) {
        this.setState({ ...res.data.data[0] })
      } else {
        message.error('')
      }
    })
    getUrl({}).then((res: any) => {
      if (res.data && res.data.success) {
        let newUrls: any = [[], [], [], [], [], [], [], [], [], [], []]
        res.data.data.map((it: any) => {
          newUrls[+it.classis].push(it)
          return null
        })
        this.setState({ urls: newUrls, newUrls })
      } else {
        message.error('')
      }
    })
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  }

  handleSubmit = () => {
    this.props.form.validateFields((err: any, values: any) => {
      let time = new Date().getTime()
      if (!err) {
        addUrl({ ...values, time }).then((res: any) => {
          notification['success']({
            message: '',
            description: `${values.title}`,
            duration: 2
          })
          if (res.data.success) {
            let { urls } = this.state
            let { classis, url, title } = values
            let newUrls = urls.map((it: any, idx: number) => {
              if (idx === +values.classis) {
                return [...it, { classis, url, title, time }]
              } else {
                return it
              }
            })
            this.setState({ newUrls, urls: newUrls })
          }
        }).catch((error: any) => {
          notification['error']({
            message: '',
            description: '',
            duration: 2
          })
        })
        this.setState({
          visible: false,
        });
      }
    });
  };

  handleCancel = () => {
    this.setState({ visible: false });
  };

  onSearch = (value: string) => {
    let { urls } = this.state
    if (value.trim() === '') {
      this.setState({newUrls: urls})
    } else {
      let newUrls = urls.map((urlArr: any, idx: number) => {
        if (idx === 0) {
          return urlArr
        } else {
          return urlArr.filter((it: any) => {
            return it.title.includes(value)
          })
        }
      })
      this.setState({ newUrls })
    }
  }

  render() {
    const { postCount, todoCount, dailyCount, footCount, photoCount, newUrls, visible, urlCount, title, url } = this.state
    const renderHeader = (
      <div className='avatar-user'>
        <Avatar size="small" src="https://front-images.oss-cn-hangzhou.aliyuncs.com/i4/5b019b627dcc672321b168667d7337e0-88-88.gif" />
        <span className="username"></span>
      </div>
    )
    const { getFieldDecorator } = this.props.form;
    return (
      <div className='my-home'>
        <div className='article-list'>
          <div className='visit-list'>
            <span></span>
            {newUrls[0].map((it: any, idx: number) => {
              return <span key={it.time}>
                <a style={{borderBottom: '1px solid #8fb6d9'}} href={it.url} target="blank">{it.title}</a><span className="int"></span>
              </span>
            })}
            <span><Icon type="plus-circle" onClick={this.showModal} /></span><span className="int"></span>
            <Modal
              title=""
              visible={visible}
              okText=""
              onOk={this.handleSubmit}
              onCancel={this.handleCancel}
            >
              <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                <Item label="" key='classis'>
                  {getFieldDecorator('classis', {
                    initialValue: '0'
                  })(
                    <Select>
                      {
                        Object.keys(urlClassis).map(it => {
                          return <Option value={it}>{urlClassis[it]}</Option>
                        })
                      }
                    </Select>
                  )}
                </Item>
                <Item label="" key='title'>
                  {getFieldDecorator('title', {
                    initialValue: title,
                    rules: [{ required: true, message: '!' }]
                  })(<Input />)}
                </Item>
                <Item label="" key='url'>
                  {getFieldDecorator('url', {
                    initialValue: url,
                    rules: [{ required: true, message: '!' }]
                  })(<Input />)}
                </Item>
              </Form>
            </Modal>
          </div>
          <p className='saohua'>{saohua[(Math.random() * 100 | 0) % 38]}</p>
          {newUrls.map((it: any, idx: number) => {
            if (idx === 0) return null
            return it.length === 0 ? null : <div className='list' key={idx}>
              <h4 style={{paddingLeft: 10}}>{urlClassis[+idx]}</h4>
              <ul style={{listStyle: 'none', padding: 0}}>
                {it.map((item: any) => {
                  return <li className="list-item" key={item.time}>
                    <a href={item.url} rel="noopener noreferrer" target='_blank' className='ellipsis'><Icon type="link" style={{margin: '0 10px'}} /> {item.title}</a>
                  </li>
                })}
              </ul>
            </div>
          })}
        </div>
        <div className='side-bar'>
          <div className="search">
            <Search
              size='small'
              placeholder=""
              onSearch={this.onSearch}
            />
          </div>
          <div className="personal">
            <Descriptions title={renderHeader} column={2} size="small">
              <Descriptions.Item label={<Icon type="wechat" />}>mywx_ky</Descriptions.Item>
              <Descriptions.Item label={<Icon type="environment" />}></Descriptions.Item>
              <Descriptions.Item label={<Icon type="github" />}><a href="https://github.com/KuaiYu95" target="blank">KuaiYu95</a></Descriptions.Item>
              <Descriptions.Item label={<Icon type="weibo-circle" />}></Descriptions.Item>
            </Descriptions>
          </div>
          <div className="statistics">
            <Row type="flex" gutter={16} justify="space-around" align="middle">
              <Col span={6}>
                <Link to='/ky/blog'><Statistic title="" value={postCount} /></Link>
              </Col>
              <Col span={6}>
                <Link to='/ky/diary'><Statistic title="" value={dailyCount} /></Link>
              </Col>
              <Col span={6}>
                <Link to='/ky/footPrint'><Statistic title="" value={footCount} /></Link>
              </Col>
              <Col span={6}>
                <Link to='/ky/pictureWall'><Statistic title="" value={photoCount} /></Link>
              </Col>
              <Col span={6}>
                <Link to='/ky/todos'><Statistic title="" value={todoCount} /></Link>
              </Col>
              <Col span={6}>
                <Statistic title="" value={urlCount} />
              </Col>
              <Col span={6}>
                <Statistic title="" value={0} />
              </Col>
              <Col span={6}>
                <Statistic title="" value={0} />
              </Col>
              <Col span={6}>
                <Statistic title="" value={0} />
              </Col>
              <Col span={6}>
                <Statistic title="" value={0} />
              </Col>
              <Col span={6}>
                <Statistic title="like" value={0} />
              </Col>
              <Col span={6}>
                <Statistic title="mark" value={0} />
              </Col>
            </Row>
          </div>
        </div>
      </div>
    )
  }
}

const User = Form.create({ name: 'register' })(UrlForm);
export default User

